body {
  background-color: #2bb7da;
  overflow-x: hidden; }

h1 span {
  color: #FFFFFF;
  font-weight: 700; }

.buttom-hamburger {
  display: block;
  position: relative;
  height: 2.5rem;
  width: 3.5rem;
  margin-right: 0;
  z-index: 1;
  cursor: pointer;
  transition: all .5s ease-in-out; }
  .buttom-hamburger span {
    display: block;
    position: relative;
    height: .3rem;
    width: 100%;
    background: #FFFFFF;
    border-radius: 9px;
    opacity: 1;
    left: 0;
    top: 0;
    transform: rotate(0deg);
    transition: all 350ms ease-in-out; }
    .buttom-hamburger span:first-child {
      transform-origin: 0% 0%; }
    .buttom-hamburger span:last-child {
      transform-origin: 0% 100%; }
    .buttom-hamburger span:not(:last-child) {
      margin-bottom: .6rem; }
  .buttom-hamburger.open span:first-child {
    transform: rotate(45deg) translate(6px, -10px); }
  .buttom-hamburger.open span:nth-child(2) {
    opacity: 0;
    transform: scale(0.8); }
  .buttom-hamburger.open span:last-child {
    transform: rotate(-45deg) translate(5px, 10px); }

.sub-menu {
  display: block;
  position: absolute;
  background-color: #2d314d;
  top: 0;
  right: 0;
  padding: 0 10%;
  transform: translate(100%, 0);
  transition: all 350ms ease-in-out; }
  .sub-menu ul {
    list-style: none;
    text-align: right;
    padding: 0;
    padding-top: 10rem; }
    .sub-menu ul li {
      padding: 1rem 0; }
      .sub-menu ul li a {
        text-decoration: none;
        color: #FFFFFF; }
  .sub-menu.openned {
    transform: translate(0, 0); }

.header {
  background-color: #2d314d;
  position: relative; }

.barra {
  display: flex;
  padding: 0;
  align-items: center;
  justify-content: space-between; }
  .barra h2 {
    color: #FFFFFF;
    text-transform: uppercase;
    font-weight: 900; }
  .barra .navegacion {
    display: none; }
    @media (min-width: 768px) {
      .barra .navegacion {
        display: block; } }
    .barra .navegacion a {
      color: #FFFFFF; }
      .barra .navegacion a:not(:last-child) {
        margin-right: 2.5rem; }
  .barra .menu-hamburger {
    display: block; }
    @media (min-width: 768px) {
      .barra .menu-hamburger {
        display: none; } }
